@import url('https://fonts.googleapis.com/css2?family=DM+Mono:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=DM+Serif+Display:ital@0;1&family=Inter:wght@100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nova+Mono&display=swap');

html{
  /*background: linear-gradient(to bottom, rgba(250, 112, 112, 0.7) 0% , white 50%);*/
  overflow-x: hidden;
  margin: 0;
}
.svg{
  fill: #2b3559;
  stroke: #2b3559;
  z-index: -1;
  position: absolute;
  top: 0;
  left:0;
}

body {
  width: 100%;
  margin: 0;
  box-sizing: border-box;
  font-family:"DM Sans", serif;
  position: relative;
}

::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #2b3559;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #bebbbb;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.navbar{
  width: 100%;
  /*background-color: white;*/
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*border-bottom: 1px solid #2b2b86;*/
  height: 14vh;
  background-color: #2b3559;
}

.navbar-left{
  display: flex;
  flex-grow: 1;
  max-width: 60%;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  color: white;
  /*border-bottom: 1px solid white;*/
  /*border-bottom: 1px solid white;*/
}

.navbar-left div{
  cursor: pointer;
  margin-left: 12%;
  font-size: 20px;
  transition: all 0.3s;
  font-weight: 300;
  padding: 0 5px;
}

.navbar-left div:hover{
  color: rgba(254, 197, 118, 0.6);
  font-weight: 500;
  padding: 0 4px;
}

.navbar-right{
  max-width: 30%;
  padding: 20px 5%;
  margin-right: 30px;
  display: flex;
}

.login{
  padding: 12px 30px;
  border: 1px solid white;
  border-radius: 5px;
  min-width: 120px;
  color: white;
  background-color: transparent;
  margin-left: 15px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: 400;
}

.login:last-child{
  background-color: #fec576;
  color: black;
  border: none;
}

.login:hover{
  color: #2b3559;
  background-color: white;
}

.login:last-child:hover{
  color: #222a46;
}

