.landing-container{
  width: 100%;
  height: 86vh;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  /*background: linear-gradient(to top right, rgb(225, 234, 241) 0%, rgba(255, 255, 255, 0) 45%);*/
  background-image: url('./components/img.png');
  background-size: 650px;
  background-position: right 10%;
  background-repeat: no-repeat;
  background-color: #2b3559;
}

.landing div{
  margin-top: 15px;
  margin-left: 6%;
}

.header{
  font-size: 42px;
  font-family:"DM Serif Display", serif;
  font-weight: 500;
  width: 90%;
  margin-left: 6%;
  text-align: left;
  color: #f6f3f3;
  margin-bottom: 1%;
}

.subheader{
  font-size: 24px;
  width: 50%;
  min-width: 300px;
  color: #f6f3f3;
  margin-left: 6%;

}

.button {
  margin-left: 6%;
  margin-top: 80px;
  margin-bottom: 120px;
  padding: 18px 45px;
  min-width: 16%;
  border-radius: 5px;
  color: #202741;
  text-transform: uppercase;
  font-size: 18px;
  font-family: "DM Sans", serif;
  letter-spacing: 0.03em;
  transition: all 0.3s;
  cursor: pointer;
  background-color: white;
  z-index: 1;
}

.button:hover{
  letter-spacing: 0.09em;
  font-weight: 500;
}

.service-container{
  min-height: 100vh;
  margin: 0 auto;
  width: 100%;
  /*background-image: linear-gradient(to bottom, white 0%, #eab2af 10%);*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.service{
  min-height: 80vh;
  width: 70%;
  background-color: rgba(248, 246, 246, 0.96);
  border-radius: 10px;
  margin: 5% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.contact-container{
  display: flex;
  width: 100%;
  min-height: 100vh;
  /*background-color: #eab2af;*/
  align-items: center;
  justify-content: space-between;
  background-color: #2b3559;
  position: relative;
}

.contact-left{
  justify-self: flex-start;
  flex: 0 0 38%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 70vh;
  justify-content: flex-start;
  background-color: #2b3559;
  margin-top: 8%;
  border-radius: 0 5px 5px 0;
  margin-left: 12%;
}

.icon{
  font-size: 55px;
  color: white;
}

.contact-left-section{
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid white;
  padding: 5% 10px;
}

.contact-left-section:first-child{
  margin-top: 8%;
}

.contact-left-section:last-child{
  border-bottom: none;
}

.icon-info{
  width: 60%;
  font-size: 19px;
  color: white;
  margin-right: 10px;
}

.contact-right{
  flex: 0 0 30%;
  min-height: 78vh;
  background-color: #eec489;
  border-radius: 10px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  gap: 15px;
  justify-content: flex-start;
  box-shadow: 1px 5px 5px 1px rgba(0, 0, 0, 0.3);
  color: black;
  margin-bottom: 3%;
  margin-top: 5%;
  margin-right: 6%;
  z-index: 2;
}

.contact-right input{
  width: 74%;
  padding: 10px 3%;
  border-radius: 12px;
  outline: none;
  border: 1px solid #2b3559;
}

.contact-right div{
  font-size: 22px;
  margin-top: 8%;
  color: black;
  border-bottom: 1px solid rgba(176, 92, 86, 0.5);
  padding: 2px 10px;
  font-weight: 500;
}

.contact-right label{
  margin-top: 2%;
  width: 80%;
  text-align: left;
  font-weight: 500;
}

.contact-right textarea{
  width: 74%;
  border: 1px solid #2b3559;
  min-height: 18vh;
  border-radius: 12px;
  padding: 1vh 3%;
}

.contact-right button{
  padding: 7px;
  border-radius: 5px;
  margin-top: 5%;
  background-color: #f6f3f3;
  width: 60%;
  color: black;
  font-size: 18px;
  font-family:"DM Sans", serif;
  cursor: pointer;
  letter-spacing: .03em;
  text-transform: uppercase;
  border: 2px solid #2b3559;
  transition: all 0.3s;
}

.contact-right button:hover{
  letter-spacing: .09em;
  font-weight: 500;
}

.contact-right button:active{
  letter-spacing: .03em;
  padding: 6px;
  width: 58%;
}

.contact-header{
  width: 30%;
  padding: 10px 20px;
  position: absolute;
  left: 5%;
  top: 5%;
  font-size: 36px;
  font-family: "DM Serif Display", serif;
  z-index:2;
}

.contact{
  position: relative;
}

.svg-done{
  fill: white;
  position: absolute;
  top:0;
  left: 0;
  z-index: 1;
  stroke: white;
}

.service-header{
  color: #222a46;
  width: 100%;
  margin-top: 1%;
  text-align: center;
  padding: 10px 20px;
  font-size: 36px;
  font-family: "DM Serif Display", serif;
  z-index:2;
}

.carousel-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 70vh;
}

.employee{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 20px;
  background-color: rgba(246, 243, 243, 0.95);
  min-height: 40vh;
  margin: 0 15px;
  min-width: 15vw;
  z-index: 1;
}

.carousel-container button{
  margin: 15px;
}

.login-container{
  width: 100%;
  height: 86vh;
  background-color: #2b3559;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-form{
  width: 75%;
  background-color: #f5f6f6;
  min-height: 100%;
  box-shadow: 1px 5px 5px 1px rgba(0, 0, 0, 0.4);
  border-radius: 40px;
  margin-bottom: 2%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  position: relative;
}

.login-left{
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  width: 35%;
  gap: 15px;
}

.login-left input{
  display: block;
  width: 84%;
  padding: 3% 3%;
  border: none;
  background-color: #e2e4f1;
  border-radius: 8px;
  margin-top: 5px;
}

.login-left button{
  width: 90%;
  padding: 2%;
  border: none;
  background-color: #3b4265;
  margin-top: 7%;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-family: "DM Sans", serif;
  font-size: 18px;
  letter-spacing: 0.05em;
}

.login-left label{
  display: block;
  margin-top: 3%;
  color: rgba(26, 32, 54, 0.9);
}

.login-form-header{
  font-size: 28px;
  font-family: "DM Sans", serif;
  font-weight: 600;
  margin-top: 15%;
}

.login-form-subheader{
  font-size: 16px;
  font-family: "DM Sans", serif;
  font-weight: 400;
  width: 85%;
  opacity: 0.7;
  padding-bottom: 20px;
  padding-right: 5%;
  border-bottom: 1px solid #ccc9c9;
}

.login-form-bottom-links{
  font-size: 16px;
  font-family: "DM Sans", serif;
  color: rgba(86, 85, 85, 0.9);
  cursor: pointer;
  width: 90%;
}

.login-form-bottom-links:hover{
  text-decoration: underline;
}

.top-border{
  border-top: 1px solid #ccc9c9;
  padding-top: 20px;
  margin-top: 4%;
}

.name-input{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 89%;
  margin-top: 2%;
}

.name-input input{
  display: block;
  width: 98%;
  padding: 6% 1% 6% 6%;
  border: none;
  background-color: #e2e4f1;
  border-radius: 8px;
  margin-top: 5px;
}

.name-input input:last-child{
  margin-left: -2%;
}

.right{
  gap: 1%;
  margin-right: 10%;
  width: 35%;
}

.shift{
  position: absolute;
  top: 2%;
  right: 2%;
  width: 46%;
  height: 96%;
  background-color: white;
  border-radius: 40px;
  background-image: url('./components/img-login.jpg');
  background-size: 650px;
  background-position: center;
  background-repeat: no-repeat;
}

.shift-right{
  right: 0;
  left: 2%;
}

.center{
  text-align: center;
}

.service-list{
  width: 100%;
  height: 60vh;
  overflow-y: hidden;
}

.service-item{
  width: 80%;
  margin: 0 auto;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  background: none;
  border-bottom: 1px solid rgb(217, 214, 214);
  align-items: center;
  box-sizing: border-box;
}

.service-item div:last-child{
  background-color: rgba(59, 66, 101, 0.2);
  width: 5%;
  padding: 5px 5px;
  text-align: center;
  border-radius: 5px;
}

.icon-down{
  margin-top: 2%;
  font-size: 26px;
  color: #2b3559;
  cursor: pointer;
  margin-bottom: 3%;
}

.icon-down:hover{
  color: #7782af;
}

.profile{
  width: 100%;
  min-height: 86vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.profile-info{
  min-height: 10vh;
  width: 100%;
  background-color: #222a46;
  color: #f6f3f3;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.profile-menu{
  min-height: 86vh;
  width: 100%;
  background-color: #f6f3f3;
}

.profile-nav{
  width: 100%;
  background-color: #d2d0d0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

}

.profile-nav-option{
  flex-grow: 1;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  /*border-radius: 5px 5px 0 0;*/
  color: black;
  /*border-bottom: 1px solid #7782af;*/
}

.option-active{
  background-color: #f3f0f0;
  color: #222a46;
  /*border-bottom: 1px solid #222a46;*/
}

.admin-panel{
  width: 100%;
  margin-top: 3%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}


.row{
  width: 80%;
  background-color: rgba(231, 228, 228, 0.5);
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  margin-bottom: 5px;
  padding: 20px;
  align-items: center;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}

.row:hover{
  background-color: #e7e4e4;
}

.row-up{
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.down{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  width: 100%;
  gap: 30px;
  min-width: 250px;
  align-self: flex-end;
  margin-top: 20px;
  border-top: 1px solid #9f9c9c;
  padding: 10px 30px;
  box-sizing: border-box;
}

.down div{
  cursor: pointer;
}

.down div:hover{
  text-decoration: underline;
}

.row:last-child{
  margin-bottom: 30px;
}

.row-up div{
  width: 30%;
  /*flex-grow: 1;*/
  text-align: center;
}

.admin div{
  width: 30%;
  text-align: left;
}


.row-up div:first-child{
  text-align: left;
  padding-left: 5%;
}

.row-up .email{
  text-align: left;
  padding-left: 10%;
}

.row-up .email-2{
  text-align: left;
  padding-right: 10%;
}

.table-row div{
  justify-content: center;
}

.data{
  display: flex;
  margin-right: 5%;
  gap: 30px;
  align-items: center;
  color: #f5f6f6;
  font-size: 18px;
}

.logout-button{
  margin-left: 5%;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  background-color: transparent;
  color: #d7a969;
  font-size: 28px;
  border: none;
}

.logout-button:hover{
  color: #f8f6f2;
}

.down-icon{
  max-width: 5%;
  color: transparent;
  cursor: pointer;
}

.row:hover .down-icon{
  color: black;
}

.main-page-link{
  cursor: pointer;
  margin-left: 12%;
  font-size: 20px;
  transition: all 0.3s;
  padding: 0 5px;
  font-weight: 300;
  color: white;
}

.main-page-link-2{
  cursor: pointer;
  margin-left: 1%;
  font-size: 20px;
  transition: all 0.3s;
  padding: 0 3px;
  font-weight: 300;
  color: #b3bace;
  height: 10vh;
  line-height: 10vh;
  align-self: flex-start;
}


.main-page-link:hover{
  color: rgba(254, 197, 118, 0.6);
  font-weight: 500;
  padding: 0 4px;
}

.main-page-link-2:hover{
  color: #8188a1;
  font-weight: 500;
}

.calendar{
  width: 90%;
  margin: 2% auto;
  z-index: 0;
  height: 72vh;
}

.overlay{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  background-color: rgba(43, 53, 89, 0.1);
}

.event-calendar-open{
  width: 60%;
  border-radius: 1px;
  background-color: #f5f6f6;
  padding: 5px 10px 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.event-calendar-open div{
  width: 92%;
  margin: 1% auto;
  padding: 5px 10px;
  color: #222a46;
  font-size: 18px;
}

.event-calendar-open textarea{
  width: 88%;
  padding: 2%;
  margin-bottom: 3%;
  height: 14vh;
  border-radius: 5px;
  border: 1px solid #bebbbb;
  font-family: "DM Sans", serif;
  outline: #706f6f;
}

.large-button{
  width: 92%;
  padding: 11px 0;
  font-size: 16px;
  border-radius: 4px;
  border: none;
  letter-spacing: 0.04rem;
  background-color: #3d4988;
  color: whitesmoke;
  text-transform: uppercase;
  font-weight: 300;
}

.event-calendar-open button:hover{
  letter-spacing: 0.07rem;
  transition: 0.2s ease-in-out;
  opacity: 0.95;
  cursor: pointer;
}

.event-calendar-open div:first-child{
  text-align: right;
  cursor: pointer;
}

.visit-service-name{
  background-color: #eae6e6;
  font-weight: 500;
}

.personal-data{
  width: 50%;
  padding: 4vh 0;
  margin: 10vh auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: rgba(227, 225, 225, 0.4);

}

.user-data{
  width: 90%;
  margin-bottom: 3%;
}

.info-group{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2% 1%;
  font-size: 18px;
}

.info-group input{
  padding: 1.5% 2%;
  width: 31%;
  border: none;
  font-size: 16px;
  background-color: #e0dcdc;
}

.info-group div:first-child{
  width: 30%;
  color: #171d3a;
  font-weight: 600;
}

.info-group div:last-child{
  width:35%;
}

.button-group{
  width: 90%;
  text-align: right;
  font-weight: 300;
  border-bottom: none;
}

.button-group button{
  min-width: 160px;
  padding: 11px 26px;
  margin-left: 1%;
  font-size: 16px;
  border-radius: 4px;
  border: none;
  letter-spacing: 0.04rem;
  background-color: #171d3a;
  color: whitesmoke;
}

.button-group button:hover{
  letter-spacing: 0.07rem;
  padding: 11px 26px;
  transition: 0.2s ease-in-out;
  opacity: 0.95;
  cursor: pointer;
}

.button-group button:first-child{
  background-color: #a23333;
}

.alert{
  position: absolute;
  bottom: 85vh;
  width: 50%;
  left: 25%;
  opacity: 0.9;
  color: whitesmoke;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
  padding: 20px 0;
  border-radius: 5px;
}

.visit-description{
  width: 90%;
  padding: 20px 0;
  text-align: justify;
  border-top: 1px solid #737d9a;
  margin-top: 20px;
}

.archiwum-input{
  width: 76%;
  padding: 10px 3%;
  margin-bottom: 40px;
  border: 1px solid #eec489;
  border-radius: 20px;
  background-color: #f5f6f6;
  color: #222a46;
  font-size: 16px;
}

.highlight {
  background-color: #eec489; /* Or any other color you prefer */
}

.new-button{
  padding: 14px 30px;
  min-width: 14%;
  border-radius: 5px;
  background-color: #2b3559;
  text-transform: uppercase;
  font-size: 16px;
  font-family: "DM Sans", serif;
  letter-spacing: 0.03em;
  transition: all 0.3s;
  cursor: pointer;
  color: white;
  z-index: 1;
  font-weight: 300;
  border:none;
  flex: 0 0 20%;
}

.new-button:hover{
  opacity: 0.95;
}

.adding-service-form{
  width: 80%;
  padding: 20px;
  border: 1px solid rgb(157, 157, 157);
  display: flex;
  justify-content: space-between;
  gap: 5%;
  margin-bottom: 3vh;
  align-items: center;
  border-radius: 4px;
}

.adding-service-form div{
  width: 100%;
}

.adding-service-form label{
  color: rgba(34, 42, 70, 0.9);
  letter-spacing: 0.03rem;
}

.adding-service-form input{
  width: 100%;
  outline: none;
  border-radius: 6px;
  padding: 10px;
  line-height: 2.5vh;
  background-color: #f5f6f6;
  font-size: 16px;
  color: #222a46;
  font-weight: 300;
  margin-top: 2px;
  border: 1px solid #949393;
}

.adding-service-form div:first-child{
  flex: 0 0 60%;
}

.add-button{
  padding: 9px 21px;
  min-width: 12%;
